@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:wght@400;700&display=swap);
*{
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

a {
   text-decoration: none;
   color: lightgray;
}

body {
 
  font-family: 'Playfair Display', serif;;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #282c34;
  color: white;
  max-width: 1100px;
  margin: 0 auto;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{padding:20px;text-align:left}.won-card{background-color:#4da375}.loss-card{background-color:#a34d5d}

.MatchDetailsCard{display:grid;grid-template-columns:1fr 1fr;padding:10px;margin-bottom:20px}.MatchDetailsCard .match-date,.MatchDetailsCard .match-venue,.MatchDetailsCard .match-winner{margin-top:10px}

.MatchSmallCard{padding:10px;margin-bottom:20px}.MatchSmallCard .match-date,.MatchSmallCard .match-venue,.MatchSmallCard .match-winner{margin-top:10px}

.TeamPage{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-template-rows:1fr 1fr 1fr;grid-column-gap:10px;grid-row-gap:10px}.team-name-section{grid-column:span 3;justify-self:start;align-self:center}.team-name{font-size:4rem;margin-bottom:20px}.win-loss-section{justify-self:center;padding:40px;text-align:center}.additional-details{text-align:right}.additional-details>p{margin-bottom:20px}.match-details-section{grid-column:span 4}.more-link{align-self:center;justify-self:center;font-size:1.5rem}

.YearSelector{text-align:right;list-style-type:none}.YearSelector li{margin-bottom:10px;margin-top:10px}

.MatchPages{display:grid;grid-template-columns:1fr 4fr;grid-column-gap:10px}.MatchPages .year-selector{justify-self:end;padding:20px}.MatchPages .page-Heading{margin:20px}

.HomePage .app-name{font-size:3rem;margin:30px auto}.HomePage .team-grids{display:grid;grid-template-columns:1fr 1fr;grid-column-gap:10px;grid-row-gap:10px}

.TeamTitle{height:200px;text-align:center;border:1px solid white;padding:20px}

